@charset "utf-8";

/* 메인서식 */

body{
  text-align: center;
  width:100%;
  margin:0 auto;
}
header h1{padding:30px 0px;}
main h2{font-size:20px; margin-bottom: 20px;}

ul{
  display:flex;
  flex-wrap:wrap;
  text-align: center;
  margin:0 auto;
}
ul li{
  width:43%; 
  list-style: none; 
  margin:15px auto;
}
ul li p{padding:8px 0px; text-align: left;font-size:15px;}
ul li img{width:210px;}

.inputData > p{
  margin:10px 0px;
}
#img_name,#product_name,#price_info{
  width:300px;
  height:30px;
  border:1px solid #ccc;
}
input[type=button]{
  width:90%;
  height:35px;
  background: #ffc72c; 
  border: none;
  cursor: pointer;
}
.btn1{
  text-align: center;
}
label.img_name,label.product_name,label.price_info{
  width:100px;
  display: inline-block;
  font-size: 14px;
}

